}

.Navbar .Lside{
    
    display: -webkit-flex;
    
    display: flex;
    -webkit-flex: 60% 1;
            flex: 60% 1;
    -webkit-justify-content: left;
            justify-content: left;
    -webkit-align-items: center;
            align-items: center;
    
}

.Navbar .Lside .Links{
    max-height: 50px;
}

